<template>
	<div class="container">
		<form @submit.prevent="agregarCita(cita, propietario)" v-if="agregar">
			<h3 class="text-center">Agregar nueva Cita</h3>
			<b-select
				v-model="propietario"
				class="form-select my-2"
				aria-label="Default select example"
			>
				<b-select-option value="" selected disabled
					>propietario</b-select-option
				>
				<b-select-option
					v-for="(item, index) in propietarios"
					:key="index"
					:value="item"
					>{{ item.nombre }}</b-select-option
				>
			</b-select>
			<b-select
				v-model="cita.mascota"
				class="form-select custom-select-lg my-2"
			>
				<b-select-option selected value="" disabled>Mascota</b-select-option>
				<b-select-option
					v-for="(item, index) in propietario.mascotas"
					:key="index"
					:value="item.nombre"
					>{{ item.nombre }}</b-select-option
				>
			</b-select>
			<b-input
				type="text"
				placeholder="Ingrese una fecha"
				class="form-control my-2"
				v-model="cita.fecha"
				onfocus="(this.type='date')"
			/>
			<b-textarea
				placeholder="Ingrese una descripcion"
				class="form-control my-2"
				v-model="cita.descripcion"
			/>
			<b-button value="" class="btn-sm btn-block btn-success" type="submit"
				>Agregar</b-button
			>
		</form>
		<div class="home">
			<table class="table">
				<thead>
					<tr>
						<th scope="col">#</th>
						<th scope="col">Mascota</th>
						<th scope="col">Propietario</th>
						<th scope="col">Fecha</th>
						<th scope="col">Descripcion</th>
					</tr>
				</thead>
				<tbody>
					<tr v-for="(item, index) in citas" :key="index">
						<th scope="row">{{ index }}</th>
						<td>{{ item.mascota }}</td>
						<td>{{ item.propietario.nombre }}</td>
						<td>{{ item.fecha }}</td>
						<td>{{ item.descripcion }}</td>
					</tr>
				</tbody>
			</table>
		</div>
	</div>
</template>

<script>
export default {
	data() {
		return {
			citas: [],
			propietarios: [],
			propietario: {},
			cita: {},
			agregar: true,
		};
	},
	created() {
		this.listarCitas();
		this.listarPropietarios();
	},
	methods: {
		agregarCita(item) {
			item.propietario = this.propietario;
			this.axios
				.post("citas/nuevo", item)
				.then((res) => {
          this.listarCitas();
					this.citas.unshift(res.data);
					this.cita = {};
					this.propietario = {};
					alert("cita agregada");
				})
				.catch((e) => {
					console.log(e.response.data.error);
				});
		},
		listarCitas() {
			this.axios
				.get("citas")
				.then((response) => {
					console.log(response.data);
					this.citas = response.data;
				})
				.catch((e) => {
					console.log("error" + e);
				});
		},
		listarPropietarios() {
			this.axios
				.get("propietarios")
				.then((response) => {
					console.log(response.data);
					this.propietarios = response.data;
				})
				.catch((e) => {
					console.log("error" + e);
				});
		},
	},
};
</script>
