<template>
	<div class="container">
		<form @submit.prevent="agregarPropietario(propietario)" v-if="agregar">
			<h3 class="text-center">Agregar nuevo usuario</h3>
			<b-input
				type="text"
				placeholder="Nombre"
				class="form-control my-2"
				v-model="propietario.nombre"
			/>
			<b-input
				type="text"
				placeholder="Documento de identidad"
				class="form-control my-2"
				v-model="propietario.dni"
			/>
			<b-input
				type="tel"
				placeholder="telefono"
				class="form-control my-2"
				v-model="propietario.telefono"
			/>
			<b-button value="" class="btn-sm btn-block btn-success" type="submit"
				>Agregar</b-button
			>
		</form>
	</div>
</template>

<script>
export default {
	data() {
		return {
			propietario: {},
			agregar: true,
		};
	},
	created() {
	},
	methods: {
		agregarPropietario(item) {
			this.axios
				.post("propietarios/nuevo", item)
				.then((res) => {
          alert('propietario agregado')
					console.log(res.data);
          this.propietario = {};
				})
				.catch((e) => {
					console.log(e.response.data.error);
				});
		}
	},
};
</script>
