<template>
	<div class="container">
		<form @submit.prevent="agregarMascota(id, mascota)" v-if="agregar">
			<h3 class="text-center">Agregar nueva Mascota</h3>
			<b-select v-model="id" class="form-select custom-select-lg my-2">
				<b-select-option selected value="" disabled
					>Propietario</b-select-option
				>
				<b-select-option
					v-for="(item, index) in propietarios"
					:key="index"
					:value="item._id"
					>{{ item.nombre }}</b-select-option
				>
			</b-select>
			<b-input
				type="text"
				placeholder="Nombre"
				class="form-control my-2"
				v-model="mascota.nombre"
			/>

			<b-input
				type="number"
				placeholder="Edad"
				class="form-control my-2"
				v-model="mascota.edad"
			/>
			<b-button value="" class="btn-sm btn-block btn-success" type="submit"
				>Agregar</b-button
			>
		</form>
	</div>
</template>

<script>
export default {
	data() {
		return {
			propietarios: [],
			mascota: {},
			id: "",
			agregar: true,
		};
	},
	created() {
		this.listarPropietarios();
	},
	methods: {
		agregarMascota(_id, mascota) {
			let nuevaMascota = { _id, mascota };
			this.axios
				.post("propietarios/nueva-mascota", nuevaMascota)
				.then((res) => {
					alert("mascota agregada");
					console.log(res.data);
					this.mascota = {};
					this.id = "";
				})
				.catch((e) => {
					console.log(e.response.data.error);
				});
		},
		listarPropietarios() {
			this.axios
				.get("propietarios")
				.then((response) => {
					console.log(response.data);
					this.propietarios = response.data;
				})
				.catch((e) => {
					console.log("error" + e);
				});
		},
	},
};
</script>
